import React from "react";
import io from "socket.io-client";

export const storeCTX = React.createContext();

let socket;

const initState = {};

function reducer(state, action) {
  //might not be the right way for react
  const prevState = { ...state };
  const length = Object.keys(prevState).length;
  prevState[length + 1] = action.payload;
  switch (action.type) {
    case "RECEIVE MESSAGE":
    {
      console.log(action.payload.topic);
      if (action.payload.topic === JSON.parse(window.localStorage.getItem("topic"))) {
        return prevState
      } else {
        return {...state}
      }
    }
    case "FETCH MESSAGE":
      return action.payload;
    default:
      return state;
  }
}

function sendChatAction(value) {
  console.log(value)
  socket.emit("chat message", value);
}

const Store = props => {
  const [newLink, setNewLink] = React.useState({});
  const [allChats, dispatch] = React.useReducer(reducer, initState);
  const [user, setUser] = React.useState("");

  //topic should not be confused with newLink, newLink fetches data from server
  //while topic is just used for UI stuffs in header component
  const [topic, setTopic] = React.useState("");

  React.useEffect(() => {
    fetch("/api/topics", { credentials: "include" })
      .then(res => res.json())
      .then(json => {
        if(json.code === 200) {
          setNewLink(json.topics);
        }
      });
  }, []);

  if (!socket) {
    socket = io(":5000")
    socket.on("chat message", function(msg) {
      dispatch({ type: "RECEIVE MESSAGE", payload: msg });
    });
  }
  return (
    <storeCTX.Provider
      value={{
        allChats,
        dispatch,
        sendChatAction,
        newLink,
        user,
        setUser,
        topic,
        setTopic
      }}
    >
      {props.children}
    </storeCTX.Provider>
  );
};

export default Store;
